<template>
  <div>
    <h2>練習二</h2>
    Account: <input type="text" @keyup="checkAccount" v-model="user.account">
      <span v-if="!isCorrectAcc" style="color: red;">請輸入有效帳號</span><br> Password: <input type="password" @keyup="checkPass"
          v-model="user.password">
      <span v-if="!isCorrectPass" style="color: red;">請輸入有效密碼</span><br>

      <button @click="logIn">Log in</button>
      <p style="color: green;" v-if="isLogIn">Log in</p>
      <p style="color: red;" v-else>Log out</p>
    </div>
</template>

<style scoped>

</style>


<script>
  export default{
    name: 'Practice2',
    data () {
      return{
        user: {
            account: '',
            password: ''
        },
        isLogIn: false,
        isCorrectAcc: true,
        isCorrectPass: true
      }
    },
    methods: {
      logIn() {
          const vm = this;
          if (vm.isCorrectAcc && vm.isCorrectPass && vm.user.account != '' && vm.user.password != '') {
              vm.isLogIn = true;
          } else {
              alert('請輸入正確的帳號密碼')
          }
      },
      checkAccount() {
          const vm = this;
          let regAccount = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,12}$/;
          if (!regAccount.test(vm.user.account)) {
              vm.isCorrectAcc = false
          } else {
              vm.isCorrectAcc = true
          }
      },
      checkPass() {
          const vm = this;
          let regPass = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/;
          if (!regPass.test(vm.user.password)) {
              vm.isCorrectPass = false
          } else {
              vm.isCorrectPass = true
          }
      }
    },
  }
</script>


