<template>
    <div>
        <h2>練習一</h2>
        <input type="text" v-model="filterText" @keyup.enter="keyIn(filterText)">
        <button @click="clearAll">Clear</button>
        <div>
            <ul v-if="filterText">
            <li v-for="(item, index) in filterData" @click="keyIn(item)" :key="index">
                {{item}}
            </li>
            </ul>
            <h2 v-if="keyWord">You have searched 
            <span style="color: red;">{{keyWord}}</span> .
            </h2>
        </div>
        <p v-if="!keyWord">Hint: Try to type J.</p>
        <p v-if="!keyWord">Hint: 可搜尋內容 Javascript, Vue, React,<br>
        Angular, Bootstrap, Webpack</p>
    </div>
</template>

<script>
export default{
    data () {
        return{
            searchData: [
                'Javascript',
                'Vue',
                'React',
                'Angular',
                'Bootstrap',
                'Webpack'
            ],
            filterText: '',
            keyWord: ''
        }
    },
    methods: {
        keyIn(el) {
            const vm = this;
            vm.keyWord = el;
            vm.filterText = '';
        },
        clearAll() {
            const vm = this;
            vm.keyWord = '';
        }
    },
    computed: {
        filterData() {
            const vm = this;
            return vm.searchData.filter((item) => {
                return item.match(vm.filterText);
            })
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    input{
        width: 20%;
        height: 22px;
        font-size: 22px;
    }
    ul{
        width: 20%;
        list-style-type: none;
        margin: 1px;
        padding: 0px;
        box-shadow: 0px 0px 5px 0px grey;
    }
    li{
        display: block;
        padding: 10px;
        background-color: #fff;
        cursor: pointer;
        transition: all .2s;
    }
    li:hover{
        background-color: rgba(0,0,0,.6);
        color: white;
    }
</style>



