<template>
  <div>
    <h2>練習三</h2>
    <input type="radio" name="identity" v-model="identity" value="admin">管理員
    <input type="radio" name="identity" v-model="identity" value="system">系統員
    <input type="radio" name="identity" v-model="identity" value="member">會員
    <div>
      <input v-model="msg" type="text" style="width: 300px; margin: 20px 0;">
      <button @click="addMsg">Submit</button>

      <ul>
          <li v-for="(item, index) in filterData" :key="index">
              <div>
                  <p>
                      <span><a @click="deleteMsg(item)" href="#" style="font-size: 20px;">&#10006;</a></span>
                      {{item.content}}
                  </p>
              </div>
          </li>
      </ul>
    </div>
  </div>
</template>

<style scoped>
  ul{
    list-style-type: none;
  }
</style>


<script>
  export default{
    name: 'Practice3',
    data () {
      return{
        identity: '',
        messageData: [
          {
            identity: 'admin',
            content: '測試用管理員留言內容',
            id: 123
          },
          {
            identity: 'system',
            content: '測試用系統員留言內容',
            id: 456
          },
          {
            identity: 'member',
            content: '測試用會員留言內容',
            id: 789
          }
        ],
        msg: ''
      }
    },
    methods: {
      addMsg() {
        const vm = this;
        const timestamp = Math.floor(Date.now())
        if (!vm.msg.trim()) {
          alert('請輸入內容')
          return
        }
        if (vm.identity == '') {
          alert('請確認你的身份')
          return;
        } else {
          let current = {
              identity: vm.identity,
              content: vm.msg,
              id: timestamp
          }
          vm.messageData.unshift(current);
          vm.msg = '';
        }
      },
      deleteMsg(item) {
        const vm = this;
        let sort = vm.messageData.map((el) => {
            return el.id
        })
        vm.messageData.splice(sort.indexOf(item.id), 1)
      }
    },
    computed: {
      filterData() {
        const vm = this;
        //       if (vm.identity == 'admin') {
        //         vm.messageData.forEach((item) => {
        //           if(item.identity == 'admin') {
        //             filttodos.push(item)
        //           }
        //         })
        //       }
        //       return filttodos;
        if (!vm.identity) {
            return vm.messageData
        } else {
            return vm.messageData.filter((item) => {
                return item.identity == vm.identity
            })
        }
      }
    }
  }
</script>


